import { AbstractControl, FormGroup } from '@angular/forms';

export function isLabelRequired(label: string, formData: FormGroup) {
  var validators: any;
  if (
    label.includes('.') &&
    typeof formData.get(label)?.validator === 'function'
  ) {
    validators = (formData.get(label) as any).validator({} as AbstractControl);
  } else if (
    formData.controls[label] &&
    typeof formData.controls[label].validator === 'function'
  ) {
    validators = (formData.controls[label] as any).validator(
      {} as AbstractControl
    );
  }
  return validators ? validators.required : false;
}
export type Direction = 'ltr' | 'rtl';
