import { ChangeDetectorRef, Component, HostListener, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { NavigationEnd, Router } from '@angular/router';
import { faArrowLeft, faArrowRight } from '@fortawesome/free-solid-svg-icons';
import { LocalizeRouterService } from '@gilsdav/ngx-translate-router';
import { TranslateService } from '@ngx-translate/core';
import { DeviceDetectorService } from 'ngx-device-detector';
import { filter } from 'rxjs';
import { getWindow as widnows } from 'ssr-window';
import { SCREEN_MD, emailPattern, namePattern } from '../../constants';
import { isLabelRequired } from '../../helper';

@Component({
  selector: 'app-contact-us',
  templateUrl: './contact-us.component.html',
  styleUrls: ['./contact-us.component.scss'],
})
export class ContactUsComponent implements OnInit {
  faArrow = faArrowRight;
  locales = this.localizeRouterService.parser.locales;
  currentUrl = '';
  isMobileOrTablet = true;
  @HostListener('window:resize', ['$event'])
  onResize(event: any) {
    if (event.target.innerWidth > SCREEN_MD) {
      this.isMobileOrTablet = false;
    } else {
      this.isMobileOrTablet = true;
    }
  }
  isLabelRequired = isLabelRequired;
  autoCompleteOff = () => {
    if (typeof widnows() === 'undefined') {
      return 'off';
    }
    const { userAgent } = widnows().navigator;
    return userAgent.includes('Mac') ? 'doNotAutoComplete' : 'off';
    // tslint:disable-next-line: semicolon
  };
  constructor(
    private router: Router,
    private spy: ChangeDetectorRef,
    private deviceDetectorService: DeviceDetectorService,
    private translate: TranslateService,
    public localizeRouterService: LocalizeRouterService
  ) {}
  formData: FormGroup = new FormGroup({});
  detectDir() {
    this.faArrow =
      this.localizeRouterService.parser.currentLang == 'ar'
        ? faArrowLeft
        : faArrowRight;
    this.spy.detectChanges();
  }
  ngOnInit(): void {
    
    this.formData = new FormGroup({
      first_name: new FormControl('', [
        Validators.required,
        Validators.pattern(namePattern),
      ]),
      last_name: new FormControl('', [
        Validators.required,
        Validators.pattern(namePattern),
      ]),
      email: new FormControl(localStorage.getItem('email'), [
        Validators.required,
        Validators.email,
        Validators.pattern(emailPattern),
      ]),
      mobile: new FormControl('', [Validators.required]),
      company: new FormControl('', []),
      data: new FormControl('', []),
    });
    this.detectDir();
    this.detectSize();
    this.router.events
      .pipe(filter((event) => event instanceof NavigationEnd))
      .subscribe(() => {
        this.detectDir();
      });
  }
  detectSize() {
    this.isMobileOrTablet =
      this.deviceDetectorService.isMobile() ||
      this.deviceDetectorService.isTablet();
  }
  noWhitespaceValidator(control: FormControl) {
    const isWhitespace = (control.value || '').trim().length === 0;
    const isValid = !isWhitespace;
    return isValid ? null : { whitespace: true };
  }
  showInputError(control: string) {
    return this.formData.get(control)?.invalid && this.getErrorMessage(control);
  }
  getErrorMessage(control: string) {
    if (this.formData.get(control)?.hasError('required')) {
      return this.translate.instant('contact-us.form.thisFieldIsRequired');
    }
    if (control === 'first_name') {
      return this.formData.get(control)?.hasError('pattern') ||
        this.formData.get(control)?.hasError('whitespace')
        ? this.translate.instant('contact-us.form.notValidFirstName')
        : null;
    }
    if (control === 'last_name') {
      return this.formData.get(control)?.hasError('pattern') ||
        this.formData.get(control)?.hasError('whitespace')
        ? this.translate.instant('contact-us.form.notValidLastName')
        : null;
    }
    if (control === 'email') {
      return this.formData.get(control)?.hasError('email') ||
        this.formData.get(control)?.hasError('pattern')
        ? this.translate.instant('contact-us.form.notValidEmail')
        : null;
    }
    if (control === 'mobile') {
      return this.formData.get(control)?.hasError('validatePhoneNumber')
        ? this.translate.instant('contact-us.form.notValidMobile')
        : null;
    }
    return null;
  }
}
